import http from '@/utils/request'

export const businessApi = {
    // 获取商家详情
    getBusinessById(businessId) {
        return http.post('BusinessController/getBusinessById', {
            businessId: businessId
        })
    },
    
    // 获取商家的食品列表
    listFoodByBusinessId(businessId) {
        return http.post('FoodController/listFoodByBusinessId', {
            businessId: businessId
        })
    },

    // 根据订单类型获取商家列表
    listBusinessByOrderTypeId(orderTypeId) {
        return http.post('BusinessController/listBusinessByOrderTypeId', {
            orderTypeId: orderTypeId
        })
    },

    // 获取食品详情
    getFoodById(params) {
        return http.post('FoodController/getFoodById', params)
    }
}

export default businessApi 