import http from '@/utils/request'

export const cartApi = {
    listCart(userId) {
        return http.post('CartController/listCart', {
            userId: userId
        });
    },

    // 保存购物车
    saveCart(params) {
        const data = new URLSearchParams();
        for (let key in params) {
            data.append(key, params[key]);
        }
        return http.post('CartController/saveCart', data);
    },

    // 更新购物车
    updateCart(params) {
        const data = new URLSearchParams();
        for (let key in params) {
            data.append(key, params[key]);
        }
        return http.post('CartController/updateCart', data);
    },

    // 删除购物车项
    removeCart(params) {
        const data = new URLSearchParams();
        for (let key in params) {
            data.append(key, params[key]);
        }
        return http.post('CartController/removeCart', data);
    }
}

export default cartApi;
