import axios from 'axios'
import router from '@/router'
import qs from 'qs'

// 创建axios实例
const service = axios.create({
    baseURL: 'http://localhost:8080/elm/',
    timeout: 10000,
    withCredentials: true,  // 允许携带cookies
    headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
    }
})

// request拦截器
service.interceptors.request.use(
    config => {
        // 获取token
        const token = localStorage.getItem('token')
        
        // 除了登录和注册请求外，其他请求都需要token
        if (!config.url.includes('login') && !config.url.includes('register') && 
            !config.url.includes('getUserInfo') && !config.url.includes('getUserById')) {
            if (token) {
                config.headers['Authorization'] = `Bearer ${token}`
            } else {
                router.push('/login')
                return Promise.reject('No token')
            }
        }

        // POST请求数据处理
        if (config.method === 'post') {
            if (config.url.includes('login')) {
                config.headers['Content-Type'] = 'application/json'
            } else {
                // 如果数据已经是 URLSearchParams，不需要再次处理
                if (!(config.data instanceof URLSearchParams)) {
                    config.data = qs.stringify(config.data)
                }
                config.headers['Content-Type'] = 'application/x-www-form-urlencoded'
            }
        }

        return config
    },
    error => {
        console.log('请求错误:', error)
        return Promise.reject(error)
    }
)

// response拦截器
service.interceptors.response.use(
    response => {
        return response.data
    },
    error => {
        if (error.message === 'No token') {
            return Promise.reject(error)
        }

        console.log('err:', error)
        if (error.response) {
            switch (error.response.status) {
                case 401:
                    // 清除token和用户信息
                    localStorage.removeItem('token')
                    sessionStorage.removeItem('user')
                    // 非登录请求才跳转
                    if (!error.config.url.includes('login')) {
                        router.push('/login')
                        alert('请重新登录')
                    }
                    break
                case 403:
                    alert('没有权限')
                    break
                case 404:
                    alert('请求的资源不存在')
                    break
                case 405:
                    console.error('请求方法不允许:', error.config.url)
                    break
                case 415:
                    console.error('不支持的媒体类型:', error.config.url)
                    break
                default:
                    alert('系统错误，请稍后重试')
            }
        } else {
            alert('网络错误，请检查网络连接')
        }
        return Promise.reject(error)
    }
)

// 请求方法封装
const http = {
    get(url, params) {
        return service.get(url, { params })
    },

    post(url, data) {
        return service.post(url, data)
    },

    put(url, data) {
        return service.put(url, data)
    },

    delete(url, params) {
        return service.delete(url, { params })
    }
}

export default http
