import request from '@/utils/request'

export const addressApi = {
    // 获取用户的配送地址列表
    listDeliveryAddressByUserId(userId) {
        const data = new URLSearchParams()
        data.append('userId', userId)
        return request.post('/DeliveryAddressController/listDeliveryAddressByUserId', data)
    },

    // 根据ID获取配送地址
    getDeliveryAddressById(daId) {
        const data = new URLSearchParams()
        data.append('daId', daId)
        return request.post('/DeliveryAddressController/getDeliveryAddressById', data)
    },

    // 更新配送地址
    updateDeliveryAddress(addressData) {
        const data = new URLSearchParams()
        for (let key in addressData) {
            data.append(key, addressData[key])
        }
        return request.post('/DeliveryAddressController/updateDeliveryAddress', data)
    },

    // 删除配送地址
    removeDeliveryAddress(daId) {
        const data = new URLSearchParams()
        data.append('daId', daId)
        return request.post('/DeliveryAddressController/removeDeliveryAddress', data)
    },

    // 保存配送地址
    saveDeliveryAddress(addressData) {
        const data = new URLSearchParams()
        for (let key in addressData) {
            data.append(key, addressData[key])
        }
        return request.post('/DeliveryAddressController/saveDeliveryAddress', data)
    }
}

export default addressApi 